@import url(https://fonts.googleapis.com/css?family=Open+Sans|Delius+Unicase|Lato);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
}

#root {
  height: 100%;
  width: 100%;
}
.currency-input-text-wrapper {
    width: 80%;
    height: 100px;
    align-self: center;
}

.currency-input-text {
    width: 100%;
    height: 100%;
    border-radius: 10px 0px 0px 10px;
    border: 0px;
    line-height: normal;
    font-size: 28px;
    font-family: 'Delius Unicase', sans-serif;
    color: #050417;
    padding-left: 8px;
    border: 2px solid rgb(223, 223, 223);
    border-right-width: 0px;
    box-sizing: border-box;
}

.currency-input-text:focus {
    border: 2px solid rgb(158, 150, 150);
    box-shadow: 0px 2px 2px 0px rgba(158, 150, 150, 1);
    border-right-width: 0px;
}
.currency-select-list-wrapper {
    width: 20%;
    height: 100px;
    border: 3px solid rgb(157, 227, 255);
    border-radius: 0px 10px 10px 0px;
    box-sizing: border-box;
    align-self: center;
    font-family: 'Lato';
    position: relative;
    cursor: pointer;
}

.currency-select-list {
    text-align: center;
    width: 100%;
    height: 140px;
    line-height: normal;
    font-weight: bold;
    color: #050417;
    background-color: #f1f1f1;
    z-index: 100;
    padding: 0;
    margin: 0;
    overflow-y: scroll;
    list-style-type: none;
}

.currency-list-header {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 26px;
    height: 100%;
    width: 100%;
}

.currency-item {
    margin: 0;
    padding: 10px 0 0 10%;
    font-size: 14px;
    display: block;
}

.currency-item:hover {
    background-color: #bbbbbb;
}

.hidden {
  display: none;
}
.currency-container {
    display: flex;
    flex-wrap: row;
    width: 100%;
    height: 32%;
    margin-bottom: 2%;
}

@media (min-width: 720px) {
    .currency-container {
        max-width: 340px;
        height: 180px;
        margin: 0px 10px 0px 10px;
        box-sizing: border-box;
        align-self: flex-end;
    }
}
.chart-container {
    width: 100%;
    height: 100%;
}
header {
    display: flex;
    width: 100%;
    height: 8%;
    justify-content: center;
    align-items: center;
}


.header-text {
    box-sizing: border-box;
    padding-top: 20px;
    font-size: 3.2em;
    font-family: 'Lato', sans-serif;
}

@media (max-width: 480px) {
    .header-text {
        padding-top: 10px;
        font-size: 2.6em;
    }
}
.container {
    display: flex;
    flex-direction: column;
    width: 96%;
    height: 100%;
    margin: 0% 2% 0% 2%;
}

.currencyview-wrapper {
    width: 100%;
    height: 84%;
    display: flex;
    flex-direction: column;
}

.chartview-wrapper {
    width: 100%;
    height: 32%;
    /* burası listopen olduğunda z-index -100 gibi bişey olacak */
}

h2 {
    margin: 0;
    padding: 0;
}

@media (min-width: 720px) {
    .container {
        align-items: center;
        justify-content: center;
    }

    .currencyview-wrapper {
        width: 720px;
        flex-wrap: wrap;
        flex-direction: row;
        align-self: center;
    }

    .chartview-wrapper {
        max-width: 360px;
        height: 180px;
        align-self: center;
        margin: auto;
    }
}
